@font-face {
    font-family: 'myFont';
    src: url('./myfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

.container_navbar {
    display: block;
    position: sticky;
    top: 0;
    z-index: 100;
}

.notify-alert {
    display: flex;
    flex-direction: row;
    align-items: center;
    min-height: 35px;
    justify-content: space-around;
}

.text {
    display: flex;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 200 !important;
    align-items: center;
    flex-direction: row;
    color: white;
}

nav {
    display: block;
}

.right {
    margin-right: 10px;
}

.header-navigation {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.navigation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 80px;
    border-bottom: 1px solid white;
}

.logo {
    color: white;
    font-size: 45px;
    text-decoration: none;
    font-weight: 500;
    font-family: 'myFont', sans-serif;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}



.navigation_list {
    display: flex;
    color: white;
    flex-direction: row;
    gap: 25px;
    list-style-type: none;
}

.navigation_list a {
    position: relative;
    text-decoration: none;
    color: white;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    display: block;
}

.navigation_list a::before {
    content: '';
    position: absolute;
    bottom: -3%;
    width: 0%;
    height: 2px;
    background-color: white;
    transition: all 0.5s linear;
}

.navigation_list a:hover::before {
    width: 100%;
}

.icon {
    display: flex;
    color: white;
    flex-direction: row;
    font-size: 20px;
    gap: 25px;
    align-items: center;
    cursor: pointer;
}

.inputB {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    right: 280px;
    top: 117px;
    transition: opacity 0.3s ease, visibility 0s 0.3s;
}

.inputField {
    width: 225px;
    height: 35px;
    padding: 10px;
    outline: none;
    border: 2px solid #0a0a0a;
    border-radius: 2px;
}

.icon_item:focus,
.icon_item:active {
    outline: none !important;
    /* Add any other custom styles here, if needed */
}

.inputB_active {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.5s linear, visibility 0s;
}


#searchIcon {
    width: 20px !important;
    height: 25px !important;
}

#heartIcon {
    width: 20px !important;
    height: 25px !important;
}

#refreshIcon {
    width: 20px !important;
    height: 25px !important;
}

#userIcon {
    width: 20px !important;
    height: 25px !important;
}

#cartIcon {
    width: 20px !important;
    height: 25px !important;
    position: relative;
}



#barIcon {
    width: 25px !important;
    height: 25px !important;
}

.hide_content {
    -webkit-tap-highlight-color: transparent;
}

/* Media Queris */
.bar {
    display: none;
    color: white;
    width: 25px;
    height: 25px;
}

.group {
    visibility: visible;
}

@media (min-width: 900px) {
    .group {
        visibility: visible !important;
    }
}

.icon {
    -webkit-tap-highlight-color: transparent !important;
}

.icon_item {
    -webkit-tap-highlight-color: transparent !important;
}

@media (max-width : 900px) {

    #cartNum {
        position: absolute;
        right: 42px;
        text-align: center;
        top: 11px;
        font-size: 15px;
        background-color: white;
        color: #0a0a0a;
        display: inline-block;
        width: 15px;
        height: 16px;
        border-radius: 64px;
    }


    .notify-alert {
        display: none;
    }

    .bar {
        display: block;
    }

    .inputB {
        z-index: 100;
    }

    .navigation_list a::before {
        bottom: -5px !important;
    }

    .navigation_list a:hover::before {
        width: 100% !important;
    }

    .navigation_list a {
        display: inline-block;
    }

    .navigation {
        min-height: 50px !important;
        border-bottom: 1px solid white;
    }

    .group {
        visibility: hidden;
        position: absolute !important;
        top: 50.7px;
        z-index: 100;
        right: 0px;
        transition: all ease-in-out;
        width: 60% !important;
        min-height: 100vh;
        background-color: #0a0a0a;
    }
   


    #searchIcon {
        width: 20px !important;
        height: 20px !important;
    }

    #userIcon {
        width: 20px !important;
        height: 20px !important;
    }

    #cartIcon {
        width: 20px !important;
        height: 20px !important;
    }

    #barIcon {
        width: 20px !important;
        height: 20px !important;
    }

    .navigation_list {
        padding-left: 19px;
        padding-top: 29px;
        gap: 35px !important;
        flex-direction: column;
        height: 84.7vh;
    }

    .icon_item:nth-child(3) {
        display: none;
    }

    .icon_item:nth-child(2) {
        display: none;
    }

    .navigation {
        justify-content: space-between !important;
        padding-left: 5px;
        padding-right: 5px;
    }

    .inputB {
        top: 52px !important;
        right: 130px !important;
    }

    .logo {
        font-size: 30px;
    }

}