#card-box-container {
    transition: all 1s linear;
    transform: scale(0.8);
    border: 1px solid rgba(41, 38, 38, 0.144);
    width: 300px;
    height: 420px;
    cursor: pointer;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    flex-shrink: 0;
}

#card-box-container:hover {
    transform: scale(0.82);
    box-shadow: 1px 1px 7px rgba(128, 128, 128, 0.384);
}
.image-card{
    border-radius: 15px;
    overflow: hidden;
}
.image-card > img{
    border-radius: 15px;
}
@media (max-width: 900px) {
    #card-box-container {
        width: 100%;
        height: auto;
        transform: scale(0.8);
    }

    #card-box-container:hover {
        box-shadow: 1px 1px 7px rgba(128, 128, 128, 0.486);
        transform: scale(0.82);
    }
}