@font-face {
  font-family: 'myFont';
  src: url('../src/Components/myfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.links{
  position: relative;
}
.links::before{
  position: absolute;
  content: '';
  bottom: -3px;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: white;
  transition: width 0.5s linear;
}

.links:hover::before{
  width: 100%;
}
#footer-heading,#logo-rajesh{
  font-family: 'myFont',sans-serif;
  font-size: 45px;
}
#logo-rajesh{
  text-align: center;
}
.App{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



